.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.elements-wrapper-absolute,
	.ornaments-wrapper {
		overflow: hidden;
		border-radius: 500px;
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-30%, 20%);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
			transform: translate(-10%, 30%);
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100px;
		transform: translate(50%, 40%);
		@include min-md {
			width: 140px;
			transform: translate(50%, 55%);
		}
		@include min-lg {
			width: 160px;
			transform: translate(50%, 60%);
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 180px;
		transform: translate(30%, 20%) scaleX(-1);
		@include min-md {
			width: 220px;
			transform: translate(10%, 20%) scaleX(-1);
		}
		@include min-lg {
			width: 240px;
			transform: translate(10%, 30%) scaleX(-1);
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(-50%, 40%) scaleX(-1);
		@include min-md {
			width: 140px;
			transform: translate(-50%, 55%) scaleX(-1);
		}
		@include min-lg {
			width: 160px;
			transform: translate(-30%, 60%) scaleX(-1);
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 3rem;
			width: 100px;
			@include min-md {
				bottom: 3rem;
				width: 120px;
			}
			@include min-lg {
				left: 5rem;
				width: 140px;
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: calc(100% - 10em) 2em 2em;
		@include max-md {
			padding: calc(100% - 8em) 2em 2em;
		}
		.image-wrapper {
			position: relative;
			width: 100%;
			aspect-ratio: 3/4;
			.rsvp-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px;
			}
		}
		.rsvp-form {
			position: relative;
			padding: 0 2rem;
			margin-bottom: 2rem;
			@include max-md {
				padding: 0 1rem;
			}
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.8);
			}
		}
	}
}
