.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 100px;
		transform: translate(-50%, -40%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(65%, -30%);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-10%, 55%);
		@include min-md {
			width: 280px;
			transform: translate(-10%, 45%);
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100px;
		transform: translate(0, 55%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 3rem;
		width: 120px;
		transform: translate(0, 65%);
		@include min-md {
			left: 5rem;
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		left: 6rem;
		width: 120px;
		transform: translate(0, 55%);
		@include min-md {
			left: 10rem;
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(10%, 55%) scaleX(-1);
		@include min-md {
			width: 240px;
			transform: translate(10%, 45%) scaleX(-1);
		}
		@include min-lg {
			width: 260px;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 1rem;
		width: 100px;
		transform: translate(0, 50%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 5rem;
		width: 100px;
		transform: translate(0, 70%);
		@include min-md {
			right: 8rem;
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6.5rem;
			align-items: center;
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				padding: 1rem 0;
				width: 30%;
				@include max-xl {
					width: 35%;
				}
				@include max-lg {
					width: 50%;
				}
				@include max-md {
					width: 70%;
				}
				@include max-sm {
					width: 100%;
				}
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: radial-gradient(ellipse, rgba(255, 255, 255, 0.8) 0%, transparent 80%);
					content: "";
					border-radius: 500px;
				}
				.cover-description {
					padding: 5rem 3rem;
					text-align: center;
					@include max-md {
						padding: 5rem 2rem;
					}
					.cover-event {
						position: relative;
						margin-bottom: 3rem;
					}
					.greeting-wrapper {
						position: relative;
						margin-bottom: 3rem;
					}
					.title {
						padding: 0.5rem 0;
						margin-bottom: 0;
					}
					p {
						margin-bottom: 0;
					}
					h6 {
						margin-bottom: 0;
						font-weight: 600;
					}
				}
			}
			.elements-widget {
				position: relative;
				width: 100%;
			}
		}
	}
}
