.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 115%;
		transform: translate(-50%, 0);
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem;
				overflow: hidden;
				@include max-md {
					padding: 2rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
		background: radial-gradient(ellipse, rgba(245, 242, 242, 0.6) 0%, transparent 100%);
		border-radius: 300px;
		padding: 2rem 1rem;
	}
}
