.couple-section {
	position: relative;
	text-align: center;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 28px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		@include max-md {
			.couple-container {
				padding: 0;
			}
		}
		.orn-99 {
			position: absolute;
			top: -8rem;
			left: 50%;
			width: 140%;
			transform: translate(-50%, 0);
			.image-element {
				height: auto;
			}
		}
		.orn-1 {
			position: absolute;
			top: -3rem;
			left: 0;
			width: 100px;
			transform: translate(-50%, 0);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
			.orn-2 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 100px;
				transform: translate(-20%, 10%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
				.orn-3 {
					position: absolute;
					bottom: 0;
					right: 0;
					width: 100px;
					transform: translate(0, 120%);
					@include min-md {
						width: 140px;
					}
					@include min-lg {
						width: 160px;
					}
				}
			}
		}
		.orn-4 {
			position: absolute;
			top: -3rem;
			right: 0;
			width: 120px;
			transform: translate(40%, 0);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-5 {
			position: absolute;
			top: 15%;
			right: 0;
			width: 120px;
			transform: translate(70%, 0) rotate(-10deg);
			@include min-md {
				top: 30%;
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 110%;
			transform: translate(-50%, 0);
		}
		.orn-7 {
			position: absolute;
			bottom: 20%;
			right: 0;
			width: 100px;
			transform: translate(20%, 0);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40%;
			transform: translate(-20%, 20%);
			@include min-md {
				left: 8%;
			}
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 40%;
			transform: translate(20%, 20%) scaleX(-1);
			@include min-md {
				right: 8%;
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
		border-radius: 500px;
		background: radial-gradient(ellipse, rgba(255, 255, 255, 0.6) 0%, transparent 80%);
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.couple {
		position: relative;
		overflow: hidden;
		padding-top: 8rem;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			background-color: var(--color-main);
			border-radius: 300px 300px 20px 20px;
			padding: 1rem;
			margin-bottom: 1rem;
			@include max-md {
				width: 90%;
			}
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
					border-radius: 300px 300px 20px 20px;
				}
			}
		}
		.couple-description {
			position: relative;
			text-align: center;
			padding: 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
