.story-section {
	position: relative;
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, -50%);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(40%, -50%);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 200px;
		}
	}
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
