.thank-section {
	position: relative;
	.orn-99 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 110%;
		height: 130%;
		transform: translate(-50%, 0);
		@include max-md {
			width: 115%;
			height: 120%;
		}
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: 50% 20%;
			}
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(65%, -30%);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-10%, 55%);
		@include min-md {
			width: 280px;
			transform: translate(-10%, 45%);
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100px;
		transform: translate(0, 55%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 3rem;
		width: 120px;
		transform: translate(0, 65%);
		@include min-md {
			left: 5rem;
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		left: 6rem;
		width: 120px;
		transform: translate(0, 55%);
		@include min-md {
			left: 10rem;
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(10%, 55%) scaleX(-1);
		@include min-md {
			width: 240px;
			transform: translate(10%, 45%) scaleX(-1);
		}
		@include min-lg {
			width: 260px;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 1rem;
		width: 100px;
		transform: translate(0, 50%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 5rem;
		width: 100px;
		transform: translate(0, 70%);
		@include min-md {
			right: 8rem;
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 5rem 0;
		overflow: hidden;
		@include max-md {
			padding: 3rem 0;
		}
		.thank {
			position: relative;
			min-height: 50vh;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			&-body {
				background: radial-gradient(ellipse, rgba(255, 255, 255, 0.6) 0%, transparent 80%);
				border-radius: 500px;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem 5rem;
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
