.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-65%, 0);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(60%, 0);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100px;
		transform: translate(-50%, 0) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(60%, 0) rotate(-10deg) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.appology-wrap {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-color: var(--color-primary);
		color: var(--color-dark);
		border-radius: 500px;
		min-height: 60vh;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
